/**
 * 数据转发模块
 * */
/* eslint-disable */
import {httpRequest} from '../../request/https'
export default {
  /**
   * 数据转发列表-page
   * */
  getForwardList: params =>  httpRequest({url: `/forward-rule/page`, params}),
  /**
   * 新增转发规则
   * */
  postForward: params => httpRequest({url: `/forward-rule`, method: 'post', params}),
  /**
   * 删除规则
   * */
  deleteForward: params => httpRequest({url: `/forward-rule/${params}`, method: 'delete'}),
  /**
   * 获取规则详情
   * */
  getForwardDetails: params => httpRequest({url: `/forward-rule/${params}`}),
  /**
   * 修改规则状态，启动或者停用
   * */
  putForwardStatus: params => httpRequest({url: `/forward-rule/status`, method: 'put', params}),
  /**
   * 修改规则信息
   * */
  putRuleInfo: params => httpRequest({url: `/forward-rule`, method: 'put', params}),
  /**
   * 转发目标列表
   * */
  getForwardTargetList: params => httpRequest({url: `/forward-target/list`, params}),
  /**
   * 新增转发目标
   * */
  postForwardTarget: params => httpRequest({url:`/forward-target`, method: 'post', params}),
  /**
   * 修改转发目标
   * */
  putForwardTarget: params => httpRequest({url:`/forward-target`, method: 'put', params}),
  /**
   * 删除转发目标
   * */
  deleteForwardTarget: params => httpRequest({url:`/forward-target/${params}`, method: 'delete'})
}



