/**
 * 在线调试
 * */
/* eslint-disable */
import {httpRequest} from '../../request/https'
export default {
  /**
   * 产品数
   **/
  getProductNum: params => httpRequest({url: '/product/count', params}),
  /**
   * 资源数
   * */
  getResNum: params => httpRequest({url: `/rule-resource/count`, params}),
  /**
   * 转发规则数
   * */
  getForwardNum: params => httpRequest({url: `/forward-rule/count`, params})
}



