import axios from 'axios'
import store from '../store'
import {BASE_URL} from '../config/proxyIp'
// eslint-disable-next-line
import router from '../router'
import {message} from 'ant-design-vue'

axios.defaults.adapter = require('axios/lib/adapters/http')
// axios.defaults.timeout = 10000
// 环境的切换
// if (process.env.NODE_ENV == 'development') {
//   axios.defaults.baseURL = 'https://www.baidu.com';
// } else if (process.env.NODE_ENV == 'debug') {
//   axios.defaults.baseURL = 'https://www.ceshi.com';
// } else if (process.env.NODE_ENV == 'production') {
//   axios.defaults.baseURL = 'https://www.production.com';
// }
let http = axios.create()
http.interceptors.request.use(config => {
  const token = store.state.user.token
  console.log('token', token)
  config.headers = {
    'authentication': token,
    'Content-Type': 'application/json'
  }
  return config
}, error => {
  return Promise.reject(error)
})
/**
 * 请求拦截
 * */
http.interceptors.request.use(config => {
  return config
}, error => {
  return Promise.error(error)
})

/**
 * 响应拦截
 * */
http.interceptors.response.use(response => {
  if (response.errorCode === 2000) {
    if (response.headers['newBearer']) {
      // 将更新token
      store.commit('SET_TOKEN', response.headers['newBearer'])
    }
  } else {
    if (response.headers['authentication']) {
      console.log('获取到的token', response.headers['authentication'])
      // 将更新token
      store.commit('SET_TOKEN', response.headers['authentication'])
    }
  }
  return response
}, error => {
  return Promise.reject(error)
})

/**
 * @param {String} url 路径
 * @param {String} method 请求方式
 * @param {String} baseUrl 请求地址
 * @param {Object} params 请求参数
 * @param {Object} headers 请求头配置
 * @param {number} timeout 请求时间
 * @param {boolean} isPolling 是否轮询
 * @return {AxiosPromise}
 **/
/**
 * 此处不能删，不然传值有问题，不要问为什么，自己百度，我也不知道
 * */
export const httpRequest = async ({url, method = 'get', params, timeout, isPolling}) => {
  return new Promise(async (resolve, reject) => {
    try {
      const result = await http({
        url: BASE_URL + url,
        params: (method === 'get' || method === 'delete') && params,
        data: ((method === 'post' || method === 'put') && params) || null,
        method: method,
        timeout: timeout || 10000,
        isPolling: isPolling || false
      })
      console.log('我请求的数据', result)
      console.log('我请求的数据码', result.data.errorCode)
      switch (result.data.errorCode) {
        case 0:
          resolve(result.data)
          break
        case 2000:
          // token过期
          message.error(result.data.msg)
          store.commit('SET_REMOVE')
          router.replace({path: '/Login'})
          break
        case 4001:
          // 请求参数错误，比如格式错误
          message.error(result.data.msg)
          break
        case 4002:
          // 未授权访问信息
          message.error(result.data.msg)
          // router.replace({path: '/Login'})
          break
        case 4003:
          // 未登录
          message.error(result.data.msg)
          store.commit('SET_REMOVE')
          router.replace({path: '/Login'})
          break
        case 4004:
          // 访问数据不存在
          message.error(result.data.msg)
          break
        case 4005:
          // 请求太多，请稍后重试
          message.error(result.data.msg)
          break
        case 5000:
          // 后端通用错误
          message.error(result.data.msg)
          break
        case 5001:
          // 加密解密失败
          message.error(result.data.msg)
          break
        case 5002:
          // 加解锁异常
          message.error(result.data.msg)
          break
      }
    } catch (error) {
      reject(error)
    }
  })
}



