/**
 * 产品模块接口
 * */
import {httpRequest} from '../../request/https'
export default {
  /**
   * 产品列表
   * */
  getProductList: params => httpRequest({url: '/product/page', params}),
  /**
   * 产品列表，不分页，list
   * */
  getProductAllList: params => httpRequest({url: '/product/list', params}),
  /**
   * 根据产品ID查询产品信息
   * */
  getProductInfo: params => httpRequest({url: `/product/${params}`}),
  /**
   * 新增产品
   * */
  postProductAdd: params => httpRequest({url: '/product', method: 'post', params}),
  /**
   * 删除产品
   * */
  deleteProduct: params => httpRequest({url: `/product/${params}`, method: 'delete'}),
  /**
   * 获取产品详情
   * */
  getProductDetails: params => httpRequest({url: `/product/${params}`}),
  /**
   * 修改产品信息
   * */
  putProductInfo: params => httpRequest({url: `/product`, method: 'put', params}),
  /**
   * 产品模型列表
   * */
  getProductModelList: params => httpRequest({url: `/product-model/list`, params}),
  /**
   * 新增产品模型
   * */
  postProductModels: params => httpRequest({url: '/product-model', method: 'post', params}),
  /**
   * 修改产品模型信息
   * */
  putProductModels: params => httpRequest({url: '/product-model', method: 'put', params}),
  /**
   * 删除产品模型信息
   * */
  deleteProductModels: params => httpRequest({url: `/product-model/${params}`, method: 'delete'}),
  /**
   * 新增产品模型属性
   * */
  postProductModelProperty: params => httpRequest({url: '/product-model/property', method: 'post', params}),
  /**
   * 修改产品模型属性信息
   * */
  putProductModelProperty: params => httpRequest({url: '/product-model/property', method: 'put', params}),
  /**
   * 删除产品模型属性信息
   * */
  deleteProductModelProperty: params => httpRequest({url: `/product-model/property/${params}`, method: 'delete'}),
  /**
   * 产品模型属性列表
   * */
  getProductModelProperty: params => httpRequest({url: '/product-model/property', params}),
  /**
   * 新增产品模型命令
   * */
  postProductModelCommand: params => httpRequest({url: '/product-model/command', method: 'post', params}),
  /**
   * 修改产品模型命令信息
   * */
  putProductModelCommand: params => httpRequest({url: '/product-model/command', method: 'put', params}),
  /**
   * 删除产品模型命令信息
   * */
  deleteProductModelCommand: params => httpRequest({url: `/product-model/command/${params}`, method: 'delete'}),
  /**
   * 产品模型命令列表
   * */
  getProductModelCommand: params => httpRequest({url: '/product-model/command', params}),
  /**
   * 产品插件开发新增
   * */
  postProductPlugin: params => httpRequest({url: '/js-plugin', method: 'post', params}),
  /**
   * 产品插件开发列表
   * */
  getProductPluginList: params => httpRequest({url: `/js-plugin`, params}),
  /**
   * 产品插件开发调试
   * */
  postDebugPlugin: params => httpRequest({url: `/js-plugin/debug`, method: 'post', params})
}



