<template>
  <div class="layout-box">
    <GlobalHeader></GlobalHeader>
    <a-layout class="layout">
      <template>
        <a-layout-sider width="200">
        <a-menu
          style="width: 256px"
          :default-selected-keys="['/product']"
          :open-keys.sync="openKeys"
          mode="inline"
          :selectedKeys="[$route.path]"
          @click="handleClick"
        >
          <a-menu-item :key="'/home'" @click="goUrl('/home')">
            <span>
              <a-icon type="bank" />
               <span>总览</span>
            </span>
          </a-menu-item>
          <a-menu-item :key="'/product'" @click="goUrl('/product')">
            <span>
              <a-icon type="lock" />
               <span>产品</span>
            </span>

          </a-menu-item>
          <a-menu-item :key="'/equipment'" @click="goUrl('/equipment')">
            <span>
              <a-icon type="apartment" />
                <span>所有设备</span>
            </span>
          </a-menu-item>
          <a-menu-item :key="'/dataForwardingList'" @click="goUrl('/dataForwardingList')">
            <span>
              <a-icon type="fund" />
            <span>数据转发</span>
            </span>
          </a-menu-item>
          <a-menu-item :key="'/resourceAllocationList'"  @click="goUrl('/resourceAllocationList')">
            <span>
                <a-icon type="file-sync" />
            <span>资源配置</span>
            </span>
          </a-menu-item>
          <a-menu-item :key="'/online'" @click="goUrl('/online')">
            <span>
              <a-icon type="sliders" />
            <span>在线调试</span>
            </span>
          </a-menu-item>
          <a-menu-item :key="'/productDocument'" @click="goUrl('/productDocument')">
            <span>
                 <a-icon type="file-markdown" />
            <span>产品文档</span>
            </span>
          </a-menu-item>
        </a-menu>
        </a-layout-sider>
        <a-layout>
          <a-layout-content
            :style="{minHeight: 'calc(100vh - 100px)'}"
          >
            <slot></slot>
          </a-layout-content>
        </a-layout>
      </template>
    </a-layout>
  </div>
</template>

<script>
import GlobalHeader from '../GlobalYayouts/GlobalHeader'
import SideMenu from '../Menu/SideMenu'

export default {
  name: 'GlobaLayoutTest',
  components: {
    GlobalHeader,
    SideMenu
  },
  data() {
    return {
      current: ['mail'],
      openKeys: ['sub1']
    }
  },
  created() {
    console.log('00999')
  },
  watch: {
    openKeys(val) {
      console.log('openKeys', val)
    }
  },
  methods: {
    goUrl(val) {
      this.$router.push(val)
    },
    handleClick(e) {
      console.log('click', e)
    },
    titleClick(e) {
      console.log('titleClick', e)
    }
  }
}
</script>

<style lang="scss" scoped>
.layout {
  width: 100%;
  position: absolute;
  bottom: 0;
  left: 0;
  top: 65px;
  //min-height: 100vh !important;
}

> > > .ant-layout-sider {
  overflow-y: auto;
  overflow-x: hidden;
  background: #ffffff;
}
</style>



