/**
 * 设备模块接口
 * */
/* eslint-disable */
import {httpRequest} from '../../request/https'
export default {
  /**
   * 根据产品ID获取到设备
   * */
  getProductDevice: params => httpRequest({url: `/device/list`, params}),
  /**
   * 删除
   * */
  deleteDevice: params => httpRequest({url: `/device/${params}`, method: 'delete'}),
  /**
   * 新增虚拟设备
   * */
  postDeviceInvented: params => httpRequest({url: `/device/invented/${params}`, method: 'post'}),
  /**
   *  设备列表
   * */
  getDeviceLis: params => httpRequest({url: `/device/page`, params }),
  /**
   * 统计设备数
   * */
  getCountDevice: params => httpRequest({url: `/device/count`, params}),
  /**
   * 新增设备
   * */
  postDevice: params => httpRequest({url: `/device`, method: 'post', params}),
  /**
   * 修改设备信息
   * */
  putDevice: params => httpRequest({url: `/device/device-label`, method: 'put', params}),
  /**
   * 修改设备状态
   * */
  putDeviceStatus: params => httpRequest({url: `/device/device-status`, method: 'put', params}),
  /**
   * 获取设备的详情
   * */
  getDeviceDetails: params => httpRequest( {url: `/device/${params}`}),
  /**
   * 获取设备命令列表
   * */
  getDeviceCommandPage: params => httpRequest({url: `/device-command/page`, params}),
  /**
   * 下发命令
   * */
  sendDeviceCommand: params =>  httpRequest({url: `/2device/command`, method: 'post', params}),
  /**
   * 获取设备最新数据(最后一条数据)
   * */
  getDeviceData: params => httpRequest({url: `/device-property/latest`, method: 'get', params}),
  /**
   * 获取设备影子列表
   * */
  getDeviceShadowList: params => httpRequest({url: `/device-property/shadow`, params}),
  /**
   * 获取设备消息追踪列表
   * */
  getDeviceMessage: (id,params) => httpRequest({url: `/device-trace/${id}`, params}),
  /**
   * 获取设备消息追踪预计结束时间
   * */
  getDeviceMessageTime: params => httpRequest({url: `/device-trace/time/${params}`}),
  /**
   * 启动消息追踪
   * */
  postDeviceMessage: params => httpRequest({url: `/device-trace`, method: 'post', params}),
  /**
   * 停止消息追踪数据
   * */
  deleteDeviceMessageData: params => httpRequest({url: `/device-trace/${params}`, method: 'delete'}),
  /**
   * 获取子设备列表
   * */
  getSubDeviceList: params => httpRequest({url: `/device/sub-device/page`, params}),
  /**
   * 删除子设备
   * */
  deleteSubDevice: params => httpRequest({url: `/device/${params}`, method: 'delete'}),
  /**
   * 新增子设备
   * */
  postSubDevice: params => httpRequest({url: `/device/sub-device`, method: 'post', params}),
  /**
   * 清除日志
   * */
  deleteDataLog: params => httpRequest({url: `/device-trace/clean-log/${params}`})
}



