/**
 * 在线调试
 * */
/* eslint-disable */
import {httpRequest} from '../../request/https'
export default {
  /**
   * 属性上报
   **/
  postFakeMessage: params => httpRequest({url: '/2device/fake-message', method: 'post', params}),
  /**
   * 选择设备后调用debug
   * */
  getDebug: params => httpRequest({url: `/device-trace/debug/${params}`})
}



