import api from '../../api'
import Cookies from 'js-cookie'
export default {
  state: {
    token: Cookies.get('token'),
    permissionList: [],
    userInfo: Cookies.get('userInfo'),
    appId: Cookies.get('appId')
  },
  mutations: {
    SET_TOKEN (state, data) {
      state.token = data
      Cookies.set('token', data)
    },
    SET_PERMISSION (state, data) {
      state.permissionList = data
    },
    SET_USER (state, data) {
      state.userInfo = JSON.stringify(data)
      Cookies.set('userInfo', JSON.stringify(data))
    },
    SET_REMOVE (state) {
      state.token = ''
      state.userInfo = ''
      state.permissionList = []
      Cookies.remove('token')
      Cookies.remove('userInfo')
      Cookies.remove('appId')
    },
    SET_APPID (state, data) {
      state.appId = data
      Cookies.set('appId', data)
    }
  },
  actions: {
    /**
     * 获取用户权限
     * */
    getPermissionList ({commit, state}) {
      return new Promise((resolve, reject) => {
        console.log('appId', Cookies.get('appId'))
        console.log('appIdss', state.appId)
        api.getAppList().then(res => {
          console.log('应用权限', res)
          commit('SET_APPID', res.data[0].id)
          if (state.appId) {
            let params = {
              appId: state.appId
            }
            api.getPermissionRouteList(params).then(res => {
              console.log('获取权限')
              const authRouter = res.data
              authRouter.map(item => Object.assign(item, {
                beRoute: item.beRoute !== 0,
                beHide: item.beHide !== 0,
                meta: {
                  title: item.title,
                  icon: item.icon,
                  url: item.path,
                  outLink: item.outLink !== 0,
                  componentName: item.name
                }
              }))
              if (authRouter && authRouter.length > 0) {
                authRouter.forEach(item => {
                  if (item['children']) {
                    let hasChildrenMenu = item['children'].filter((i) => {
                      return !i.hidden || i.hidden === false
                    })
                    if (hasChildrenMenu == null || hasChildrenMenu.length === 0) {
                      item['hidden'] = true
                    }
                  }
                })
                commit('SET_PERMISSION', authRouter)
              }
              resolve(res)
            }).catch(err => {
              console.log('错误', err)
              reject(err)
            })
          }
        })
      })
    }
  }
}



