/**
 * 数据转发模块
 * */
/* eslint-disable */
import {httpRequest} from '../../request/https'
export default {
  /**
   * 规则资源列表-page
   * */
  getResourceList: params => httpRequest({url:`/rule-resource/page`, params}),
  /**
   * 规则资源列表-list
   * */
  getResourceAllList: params => httpRequest({url: `/rule-resource/list`, params}),
  /**
   * 修改资源信息
   * */
  putResourceInfo: params => httpRequest({url: `/rule-resource`, method: 'put', params}),
  /**
   * 删除资源
   * */
  deleteResource: params => httpRequest({url: `/rule-resource/${params}`, method: 'delete'}),
  /**
   * 新增资源
   * */
  postResource: params => httpRequest({url: `/rule-resource`, method: 'post', params}),
  /**
   * 修改资源状态
   * */
  putResourceStatus: params => httpRequest({url: `/rule-resource/resource-status`, method: 'put', params}),
  /**
   * 获取资源信息
   * */
  getResourceInfo: params => httpRequest({url: `/rule-resource/${params}`})
}



